import React, { SyntheticEvent, useEffect } from "react";
import "./App.css";
import Cropper from "cropperjs";
import "cropperjs/dist/cropper.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import ProgressBar from "react-bootstrap/ProgressBar";
import Disclaimer from "./Disclaimer";
import { InferenceSession, Tensor } from "onnxruntime-web";
import Pica from "pica";

type ID = {
    name: string;
    height: number;
    width: number;
};

const US_PASSPORT: ID = {
    name: "us-passport",
    height: 600,
    width: 600,
};

function adjustGuide(id_type: string) {
    let b1 = document.getElementsByClassName("dashed-h")[0];
    b1.setAttribute("class", "cropper-dashed dashed-" + id_type + "-h1");
    let b2 = document.createElement("span");
    b2.setAttribute("class", "cropper-dashed dashed-" + id_type + "-h2");
    b1.after(b2);
}

function App() {
    const [cropper, setCropper] = React.useState<Cropper>();
    const [imgURL, setImgURL] = React.useState<string>();
    const [resultImgURL, setResultImgURL] = React.useState<string>();
    const [composedResultImgURL, setComposedResultImgURL] = React.useState<string>();

    // eslint-disable-next-line
    const [currentID, setCurrentID] = React.useState<ID>(US_PASSPORT);
    const [progressPercent, setProgressPercent] = React.useState<number>(0);

    const MODEL_INPUT_WIDTH = 320 as number;
    const MODEL_INPUT_HEIGHT = 320 as number;

    function reset() {
        cropper?.destroy();
        setCropper(undefined);
        setImgURL("");
        setResultImgURL("");
        setComposedResultImgURL("");
        setProgressPercent(0);
        (document.getElementById("formFile") as HTMLInputElement).value = "";
    }
    function inProgress() {
        return progressPercent > 0 && progressPercent < 100;
    }

    const populateCropper = (e: SyntheticEvent<HTMLImageElement>) => {
        const thisImg = e.currentTarget;
        let cropper = new Cropper(thisImg, {
            aspectRatio: 1,
            viewMode: 1,
            minCropBoxWidth: 60,
            minCropBoxHeight: 60,
            ready: (_) => {
                adjustGuide(US_PASSPORT.name);
            },
        });
        setCropper(cropper);
    };

    const composeResultImg = (e: SyntheticEvent<HTMLImageElement>) => {
        const img = e.currentTarget;
        const canvas = document.createElement("canvas");
        const h = img.naturalHeight;
        const w = img.naturalWidth;
        canvas.height = 2 * h;
        canvas.width = 3 * w;
        const context = canvas.getContext("2d");
        if (context) {
            context.fillStyle = "white";
            context.fillRect(0, 0, canvas.width, canvas.height);
            for (let i = 0; i < 2; i++) {
                for (let j = 0; j < 2; j++) {
                    context.drawImage(img, (0.5 + i) * w, j * h);
                }
            }
            context.beginPath();
            // draw horizontal lines
            context.moveTo(0.5 * w, h);
            context.lineTo(2.5 * w, h);
            // draw vertical lines
            for (let i = 0; i < 3; i++) {
                context.moveTo((i + 0.5) * w, 0);
                context.lineTo((i + 0.5) * w, 2 * h);
            }
            context.stroke();
            setComposedResultImgURL(canvas.toDataURL("image/jpeg"));
        }
    };

    const imageDataToFeeds = (img: ImageData): Promise<InferenceSession.OnnxValueMapType> => {
        var floatArr = new Float32Array(MODEL_INPUT_WIDTH * MODEL_INPUT_HEIGHT * 3);
        var floatArr2 = new Float32Array(MODEL_INPUT_WIDTH * MODEL_INPUT_HEIGHT * 3);
        var j = 0;
        for (let i = 0; i < img.data.length; i++) {
            if (i % 4 !== 0) {
                floatArr[j] = (img.data[i] as number) / 255; // red   color
                j = j + 1;
            }
        }
        var k = 0;
        for (let i = 0; i < floatArr.length; i += 3) {
            floatArr2[k] = floatArr[i]; // red   color
            k = k + 1;
        }
        var l = 102400;
        for (let i = 1; i < floatArr.length; i += 3) {
            floatArr2[l] = floatArr[i]; // red   color
            l = l + 1;
        }
        var m = 204800;
        for (let i = 2; i < floatArr.length; i += 3) {
            floatArr2[m] = floatArr[i]; // red   color
            m = m + 1;
        }
        const feeds = { "input.1": new Tensor("float32", floatArr2, [1, 3, MODEL_INPUT_WIDTH, MODEL_INPUT_HEIGHT]) };
        return Promise.resolve(feeds);
    };

    const inferenceResultToImageData = (r: InferenceSession.ReturnType): Promise<ImageData> => {
        const canvas = document.createElement("canvas") as HTMLCanvasElement;
        const ctx = canvas.getContext("2d");
        if (ctx) {
            const pred = Object.values(r)[0];
            var imgData = ctx.createImageData(MODEL_INPUT_WIDTH, MODEL_INPUT_HEIGHT);
            for (let i = 0; i < pred.data.length * 4; i += 4) {
                var pixelIndex = i;
                var t = i / 4;
                var p = pred.data[t] as number;
                imgData.data[pixelIndex] = Math.round(p * 255);
                imgData.data[pixelIndex + 3] = 0xff;
            }
            return Promise.resolve(imgData);
        }
        return Promise.reject("Failed to get context");
    };

    const imageDataToCanvas = (imgData: ImageData): Promise<HTMLCanvasElement> => {
        const canvas = document.createElement("canvas") as HTMLCanvasElement;
        canvas.width = imgData.width;
        canvas.height = imgData.height;
        const ctx2 = canvas.getContext("2d");
        ctx2?.putImageData(imgData, 0, 0);
        return Promise.resolve(canvas);
    };

    const applyBackgroundMask = (imgAndMask: ImageData[]): Promise<ImageData> => {
        let img = imgAndMask[0];
        const mask = imgAndMask[1];
        for (let i = 0; i < mask.data.length; i += 4) {
            let v = 0;
            v = mask.data[i];
            if (v < 250) {
                img.data[i] = 0xff;
                img.data[i + 1] = 0xff;
                img.data[i + 2] = 0xff;
            }
            img.data[i + 3] = mask.data[i + 3];
        }
        return Promise.resolve(img);
    };

    async function prepareModel(urls: string[]): Promise<Uint8Array> {
        let totalSize = 0 as number;
        let arrays = await Promise.all(
            urls
                .map((u) => {
                    return fetch(u);
                })
                .map(async (p) => {
                    let a = await (await p).arrayBuffer();
                    return new Uint8Array(a);
                })
        );

        arrays.forEach((a) => {
            totalSize += a.length;
        });

        let ret = new Uint8Array(totalSize);
        let counter = 0;
        arrays.forEach((v, _) => {
            ret.set(v, counter);
            counter += v.length;
        });
        return Promise.resolve(ret);
    }

    const handleSubmit = (event: React.FormEvent) => {
        event.preventDefault();
        if (cropper == null) {
            alert("select and crop a photo before uploading.");
            return;
        }

        const ctx = cropper
            .getCroppedCanvas({
                maxHeight: MODEL_INPUT_HEIGHT,
                minHeight: MODEL_INPUT_HEIGHT,
                maxWidth: MODEL_INPUT_WIDTH,
                minWidth: MODEL_INPUT_WIDTH,
            })
            .getContext("2d", { alpha: true });

        if (ctx) {
            var imageData = ctx.getImageData(0, 0, MODEL_INPUT_WIDTH, MODEL_INPUT_HEIGHT);
            const to = document.createElement("canvas") as HTMLCanvasElement;
            to.width = currentID.width;
            to.height = currentID.height;
            const pica = Pica();
            let origImgData = cropper
                .getCroppedCanvas({
                    maxWidth: currentID.width,
                    minWidth: currentID.width,
                    maxHeight: currentID.height,
                    minHeight: currentID.height,
                })
                .getContext("2d")
                ?.getImageData(0, 0, currentID.width, currentID.height) as ImageData;

            imageDataToFeeds(imageData)
                .then((feeds: InferenceSession.OnnxValueMapType) => {
                    prepareModel([
                        "/static/u2net_human_seg.onnx.aa",
                        "/static/u2net_human_seg.onnx.ab",
                        "/static/u2net_human_seg.onnx.ac",
                        "/static/u2net_human_seg.onnx.ad",
                        "/static/u2net_human_seg.onnx.ae",
                        "/static/u2net_human_seg.onnx.af",
                        "/static/u2net_human_seg.onnx.ag",
                        "/static/u2net_human_seg.onnx.ah",
                        "/static/u2net_human_seg.onnx.ai",
                        "/static/u2net_human_seg.onnx.aj",
                        "/static/u2net_human_seg.onnx.ak"
                    ])
                        .then(InferenceSession.create)
                        .then((sess: InferenceSession) => {
                            console.log("session loaded", sess);
                            console.log("session loaded", sess.inputNames);
                            sess.run(feeds)
                                .finally(() => {
                                    setProgressPercent(75);
                                })
                                .then(inferenceResultToImageData)
                                .then(imageDataToCanvas)
                                .finally(() => {
                                    setProgressPercent(80);
                                })
                                .then((from: HTMLCanvasElement) => {
                                    pica.resize(from, to)
                                        .then((result: HTMLCanvasElement) => {
                                            const ctx = result.getContext("2d");
                                            if (ctx) {
                                                let filterData = ctx.getImageData(
                                                    0,
                                                    0,
                                                    currentID.width,
                                                    currentID.height
                                                );
                                                return Promise.resolve([origImgData, filterData]);
                                            }
                                            return Promise.reject("error generate filter and original image data");
                                        })
                                        .then(applyBackgroundMask)
                                        .finally(() => {
                                            setProgressPercent(90);
                                        })
                                        .then(imageDataToCanvas)
                                        .then((c: HTMLCanvasElement) => {
                                            setResultImgURL(c.toDataURL("image/jpeg"));
                                        })
                                        .finally(() => {
                                            setProgressPercent(100);
                                        });
                                });
                        })
                        .finally(() => {
                            setProgressPercent(50);
                        });
                })
                .finally(() => {
                    setProgressPercent(50);
                });
        }
    };

    return (
        <>
            <Container className="p-3">
                <Container className="p-5 mb-4 bg-light rounded-3">
                    <h1 className="header text-center"> Welcome To freeidphoto.com </h1>
                </Container>
                <Container className="row mb-4">
                    <Container className="col">
                        <ProgressBar animated hidden={progressPercent === 0} now={progressPercent} />
                    </Container>
                </Container>
                <Container className="row">
                    <Container className="col-lg-4">
                        <Container>
                            <h3>Instruction</h3>
                            <ol>
                                <li>
                                    <b>Only US passport photo is supported (for now). </b>
                                </li>
                                <li>
                                    See&nbsp;
                                    <a
                                        href="https://travel.state.gov/content/travel/en/passports/how-apply/photos.html"
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        here
                                    </a>
                                    &nbsp;first to know the requirements.
                                </li>
                                <li>
                                    Background does not need to be 100% white but avoid photos with busy background for
                                    better results.
                                </li>
                                <li>
                                    Simply position the two green bars to cover the top of head and the bottom of chin.
                                </li>
                            </ol>
                        </Container>
                        <Container>
                            <Form.Group controlId="formFile" className="mb-3">
                                <Form.Label>Select your photo:</Form.Label>
                                <Form.Control
                                    type="file"
                                    onChange={(e: React.FormEvent) => {
                                        e.preventDefault();
                                        const files = (e.target as HTMLInputElement).files;
                                        if (files && files.length > 0 && files[0].type.match(/^image\//)) {
                                            let reader = new FileReader();
                                            reader.onload = (e) => {
                                                setImgURL(e.target?.result as string);
                                            };
                                            reader.readAsDataURL(files[0]);
                                        }
                                    }}
                                />
                            </Form.Group>
                            <Button variant="primary" onClick={handleSubmit} disabled={inProgress() || !imgURL}>
                                Submit
                            </Button>
                            <Button variant="danger" className="float-end" onClick={reset} disabled={inProgress()}>
                                Reset
                            </Button>
                        </Container>
                    </Container>
                    <Container className="col-lg-8 text-center mt-3">
                        <Container hidden={progressPercent === 100 || !imgURL}>
                            <img alt="" src={imgURL} onLoad={populateCropper}></img>
                        </Container>
                        <Container hidden={!resultImgURL || inProgress()}>
                            <h2>Click the image to download.</h2>
                            <a href={composedResultImgURL} download="composed_result.jpeg">
                                <img alt="" src={composedResultImgURL}></img>
                            </a>
                            <a href={resultImgURL} download="result.jpeg">
                                <img width={200} alt="" src={resultImgURL} onLoad={composeResultImg}></img>
                            </a>
                        </Container>
                    </Container>
                </Container>

                <Container className="mt-3">
                    <h3 className="text-center">Why do I make this website?</h3>
                    <p>
                        There are so many other ID photo websites that claim to be free. However, some only produces
                        greyscale photos. Some does not remove background. Some prints their watermarks to make result
                        photos unusable. They eventually want you to pay in order to get what you want: a white
                        background photo that is ready to print on a 6" * 4" photo paper. This website is FREE to use
                        and will always be.
                    </p>
                    <p>
                        Printing yourself at CVS, Walgreens or other stroes is less than $1 for 4 photos while photo
                        stores charge more than $10 for only 2 photos.
                    </p>
                    <p>
                        This is just a hobby project. <b>Only US passport photo is supported (for now).</b>Your
                        feedback, suggestions, and kudos are welcomed and appreciated. Please email me at
                        <a href="mailto:freeidphoto@gmail.com"> freeidphoto@gmail.com</a>.
                    </p>
                </Container>
                <Container className="row mt-5">
                    <Disclaimer />
                </Container>
            </Container>
        </>
    );
}

export default App;
